@charset "UTF-8";
:root {
  --color: red;
}

/* Цифровые значения насыщенности шрифтов, которые позволяют задавать больше, чем нормальное и полужирное начертание. */
/* font-weight: 100;  Тонкий (Волосяной); Thin (Hairline) */
/* font-weight: 200;  Дополнительный светлый (Сверхсветлый); Extra Light (Ultra Light) */
/* font-weight: 300;  Светлый; Light */
/* font-weight: 400;  Нормальный; Normal */
/* font-weight: 500;  Средний; Medium */
/* font-weight: 600;  Полужирный; Semi Bold (Demi Bold) */
/* font-weight: 700;  Жирный; Bold */
/* font-weight: 800;  Дополнительный жирный (Сверхжирный); Extra Bold (Ultra Bold) */
/* font-weight: 900;  Черный (Густой); Black (Heavy) */
/* Для шрифтов, которые предоставляют только normal и bold начертания, 100-500 normal, и 600-900 bold. */
@font-face {
  font-weight: normal;
  font-family: "roboto-example";
  font-style: normal;
  font-display: swap;
  src: url("../../fonts/RobotoRegular/RobotoRegular.woff") format("woff"), url("../../fonts/RobotoRegular/RobotoRegular.woff2") format("woff2");
}
@font-face {
  font-weight: bold;
  font-family: "roboto-example";
  font-style: normal;
  font-display: swap;
  src: url("../../fonts/RobotoBold/RobotoBold.woff") format("woff"), url("../../fonts/RobotoBold/RobotoBold.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

::placeholder {
  color: #afafaf;
  opacity: 1;
  transition: opacity 0.25s;
}

:focus::placeholder {
  opacity: 0;
}

::selection {
  color: #fff;
  background-color: #f99406;
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100vh;
  overflow-x: hidden;
  color: inherit;
  font: 16px/1.5 "roboto-example", sans-serif;
  background-color: inherit;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  /* optimizeSpeed */
}

ul[class],
ol[class] {
  padding: 0;
  list-style: none;
}

body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

input,
button,
textarea,
select {
  font: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: polygon(0 0, 0 0, 0 0);
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation: none !important;
  }
}
.container {
  width: 100%;
  margin: 0 auto;
}

@media (min-width: 320px) {
  .container {
    max-width: 320px;
  }
}
@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}
